<template>
  <div class="drawimg3">
    <div>canvas插入video</div>
    <video
      style="position: relative;  object-fit: fill;"
      preload="auto"
      controls="controls"
      loop="loop"
      id="video1"
      playsinline
      x-webkit-airplay
      webkit-playsinline
      x5-video-player-type="h5"
      src="../assets/video2.mp4"
    ></video>
    <canvas
      ref="drawimg3"
      :width="canvas.width"
      :height="canvas.height"
      class="drawimg3Style"
    >你的浏览器不支持 canvas，请升级你的浏览器。</canvas>
  </div>
</template>

<script>
export default {
  name: "drawimg3",
  data() {
    return {
      canvas: {
        width: 830,
        height: 830
      },
      R: 0
    };
  },
  mounted() {
    this.drawcanvas();
  },
  destroyed() {},
  methods: {
    drawcanvas() {
      let canvas = this.$refs.drawimg3;
      let ctx = canvas.getContext("2d");
      let timer = null;
      let video = document.getElementById("video1");
      // ctx.translate(this.canvas.width / 2, this.canvas.height / 2);
      let [x0, y0] = [0, 0];
      this.R = this.canvas.width; //设置圆半径
      ctx.clearRect(0, 0, canvas.width, canvas.height); //清除画布
      this.drawpic(ctx, timer, video, this.R);
    },

    drawpic(ctx, timer, video, R) {
      ctx.save();
      video.play();
      timer = setInterval(() => {
        // if (video.currentTime >= 3.8) {
        //   //视频时间在3.8s时停止
        //   video.pause();
        //   clearInterval(timer);
        // }
        ctx.drawImage(video, 0, 0, R, R); //绘制视频
      }, 16);
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import "@/styles/global.scss";
/* a {
  color: #42b983;
} */
.drawimg3 {
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  /*   width: vw(1920);
  height: vh(1080); */
  video {
    display: none;
  }
  .drawimg3Style {
    border: 1px red solid;
  }
}
</style>



