<template>
  <div class="drawimg4">
    <div class="playoutbox">
      <div class="fontStyle">canvas插入video、播放暂停按钮</div>
      <video
        style="position: relative;  object-fit: fill;"
        preload="auto"
        controls="controls"
        loop="loop"
        id="video4"
        playsinline
        x-webkit-airplay
        webkit-playsinline
        x5-video-player-type="h5"
        src="../assets/video2.mp4"
      ></video>
    </div>
    <div class="iconoutbox" @mouseover="changeActive()" @mouseout="removeActive()">
      <div class="iconbox" ref="iconboxflag" :class="{active:isactive}">
      <img src="../assets/play.png" alt v-if="isshowStatus" @click="play" />
      <img src="../assets/pause.png" alt v-if="!isshowStatus" @click="pause" />
      </div>
    </div>
    <canvas
      ref="drawimg4"
      :width="canvas.width"
      :height="canvas.height"
      class="drawimg4Style"
    >你的浏览器不支持 canvas，请升级你的浏览器。</canvas>
  </div>
</template>

<script>
export default {
  name: "drawimg4",
  data() {
    return {
      canvas: {
        width: 830,
        height: 830
      },
      R: 0,
      isshowStatus: false,
      isactive:false
    };
  },
  mounted() {
    this.drawcanvas();
  },
  destroyed() {},
  methods: {
    drawcanvas() {
      let canvas = this.$refs.drawimg4;
      let ctx = canvas.getContext("2d");
      let timer = null;
      let video = document.getElementById("video4");
      // ctx.translate(this.canvas.width / 2, this.canvas.height / 2);
      let [x0, y0] = [0, 0];
      this.R = this.canvas.width; //设置圆半径
      ctx.clearRect(0, 0, canvas.width, canvas.height); //清除画布
      this.drawpic(ctx, timer, video, this.R);
    },

    drawpic(ctx, timer, video, R) {
      ctx.save();
      video.play();
      timer = setInterval(() => {
        // if (video.currentTime >= 3.8) {
        //   //视频时间在3.8s时停止
        //   video.pause();
        //   clearInterval(timer);
        // }
        ctx.drawImage(video, 0, 0, R, R); //绘制视频
      }, 16);
    },
    play() {
      let video = document.getElementById("video4");
      this.isshowStatus = !this.isshowStatus;
      video.play();
    },
    pause() {
      let video = document.getElementById("video4");
      this.isshowStatus = !this.isshowStatus;
      video.pause();
    },
    // 鼠标移入加入class
  changeActive(){
   this.isactive=true;
  },
  removeActive(){
   this.isactive=false
  }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import "@/styles/global.scss";
/* a {
  color: #42b983;
} */
.drawimg4 {
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  position: relative;
  /* width: vw(1920);
  height: vh(830); */

  .drawimg4Style {
    border: 1px red solid;
  }
  .playoutbox {
    width: 100%;
    height: 100%;
    position: absolute;
    .fontStyle {
      font: bold vh(24) "微软雅黑";
      color: yellow;
      text-indent: vw(20);
      line-height: vh(60);
    }
    video {
      display: none;
    }
  }
  .iconoutbox {
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 9;
    .iconbox{
      display: none;
    }
    .active{
      display: block;
    }
  }
}
</style>



