<template>
  <div class="drawimg1">
    <div>canvas插入图片</div>
    <canvas
      ref="drawimg1"
      :width="canvas.width"
      :height="canvas.height"
      class="drawimg1Style"
    >你的浏览器不支持 canvas，请升级你的浏览器。</canvas>
  </div>
</template>

<script>
export default {
  name: "drawimg1",
  data() {
    return {
      canvas: {
        width: 830,
        height: 830
      },
      R: 0
    };
  },
  mounted() {
    this.drawcanvas();
  },
  destroyed() {},
  methods: {
    drawcanvas() {
      let canvas = this.$refs.drawimg1;
      let ctx = canvas.getContext("2d");
      // ctx.translate(this.canvas.width / 2, this.canvas.height / 2);
      let [x0, y0] = [0, 0];
      this.R = this.canvas.width; //设置圆半径
      ctx.clearRect(0, 0, canvas.width, canvas.height); //清除画布
      this.drawpic(ctx, x0, y0, this.R);
    },

    drawpic(ctx, x0, y0, R) {
      ctx.save();
      var backImg = new Image();
      backImg.src = require("../assets/1.jpg");
      backImg.onload = () => {
        var patten = ctx.createPattern(backImg, "repeat");
        ctx.fillStyle = patten;
        ctx.fillRect(x0, y0, R, R);
      };
      ctx.restore();
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import "@/styles/global.scss";
/* a {
  color: #42b983;
} */
.drawimg1 {
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  /*   width: vw(1920);
  height: vh(1080); */
  .drawimg1Style {
    border: 1px red solid;
  }
}
</style>



