<template>
  <div class="drawimg2">
    <div>canvas插入另外一个canvas</div>
    <canvas
      ref="drawimg2"
      :width="canvas.width"
      :height="canvas.height"
      class="drawimg2Style"
    >你的浏览器不支持 canvas，请升级你的浏览器。</canvas>
  </div>
</template>

<script>
export default {
  name: "drawimg2",
  data() {
    return {
      canvas: {
        width: 800,
        height: 800
      },
      R: 0
    };
  },
  mounted() {
    this.drawcanvas();
  },
  destroyed() {},
  methods: {
    drawcanvas() {
      let canvas = this.$refs.drawimg2;
      let ctx = canvas.getContext("2d");
      // ctx.translate(this.canvas.width / 2, this.canvas.height / 2);
      let [x0, y0] = [0, 0];
      this.R = this.canvas.width; //设置圆半径
      ctx.clearRect(0, 0, canvas.width, canvas.height); //清除画布
      this.drawpic(ctx, x0, y0, this.R);
    },
    drawpic(ctx, x0, y0, R) {
      ctx.save();
      var backImg = this.drawcreatbgcanvas();
      var patten = ctx.createPattern(backImg, "repeat");
      ctx.fillStyle = patten;
      ctx.fillRect(0, 0, 800, 800);
      ctx.restore();
    },
    drawcreatbgcanvas() {
      var bgcanvas = document.createElement("canvas");
      bgcanvas.width = 100;
      bgcanvas.height = 100;
      var bgctx = bgcanvas.getContext("2d");
      this.drawStar(bgctx, 50, 50, 50, 0);
      return bgcanvas;
    },
    drawStar(ctx, x, y, R, rot) {
      ctx.save();

      ctx.translate(x, y);
      ctx.rotate((rot / 180) * Math.PI);
      ctx.scale(R, R);
      this.Starpath(ctx);
      ctx.fillStyle = "yellow";
      ctx.fill();
      ctx.restore();
    },
    Starpath(ctx) {
      ctx.beginPath();
      for (var i = 0; i < 5; i++) {
        ctx.lineTo(
          Math.cos(((18 + i * 72) / 180) * Math.PI),
          -Math.sin(((18 + i * 72) / 180) * Math.PI)
        );
        ctx.lineTo(
          Math.cos(((54 + i * 72) / 180) * Math.PI) * 0.5,
          -Math.sin(((54 + i * 72) / 180) * Math.PI) * 0.5
        );
      }
      ctx.closePath();
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import "@/styles/global.scss";
/* a {
  color: #42b983;
} */
.drawimg2 {
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  /*   width: vw(1920);
  height: vh(1080); */
  .drawimg2Style {
    border: 1px red solid;
  }
}
</style>



