<template>
  <div class="drawimg0">
  <div>canvas画五角星和线条样式、连接方式</div>
    <canvas
      ref="drawimg0"
      :width="canvas.width"
      :height="canvas.height"
      class="drawimg0Style"
    >你的浏览器不支持 canvas，请升级你的浏览器。</canvas>
  </div>
</template>

<script>
export default {
  name: "drawimg0",
  data() {
    return {
      canvas: {
        width: 630,
        height: 830
      },
      R: 0
    };
  },
  mounted() {
    this.drawcanvas();
  },
  destroyed() {},
  methods: {
    drawcanvas() {
      let canvas = this.$refs.drawimg0;
      let ctx = canvas.getContext("2d");
      ctx.translate(this.canvas.width / 2, this.canvas.height / 2);
      let [x0, y0] = [0, 0];
      this.R = this.canvas.width / 2.5; //设置圆半径
      ctx.clearRect(0, 0, canvas.width, canvas.height); //清除画布
      this.drawScale1(ctx, x0, y0, this.R);
      this.drawLine1(ctx, x0, y0, this.R);
      this.drawStar(ctx, x0, y0, 190, 40);
      this.drawpic(ctx, x0, y0, this.R)
    },

    drawScale1(ctx, x0, y0, R) {
      ctx.save();
      ctx.beginPath();
      ctx.moveTo(0, 0);
      ctx.lineTo(R, R);
      ctx.lineTo(0, R);
      ctx.closePath();

      ctx.lineWidth = "10";
      ctx.fillStyle = "red";
      ctx.strokeStyle = "#522";

      ctx.fill();
      ctx.stroke();
      ctx.restore();
    },
    drawLine1(ctx, x0, y0, R) {
      ctx.save();
      ctx.beginPath();
      ctx.moveTo(-20, -200);
      ctx.lineTo(-R, -R);
      ctx.lineTo(0, -R);

      ctx.lineCap = "round";
      ctx.lineWidth = "10";
      ctx.strokeStyle = "#522";

      ctx.stroke();
      ctx.restore();
    },
    drawStar(ctx, x0, y0, R, r) {
      ctx.save();
      ctx.beginPath();

      for (var i = 0; i < 5; i++) {
        ctx.lineTo(
          Math.cos(((18 + i * 72) / 180) * Math.PI) * R + x0,
          -Math.sin(((18 + i * 72) / 180) * Math.PI) * R + y0
        );
        ctx.lineTo(
          Math.cos(((54 + i * 72) / 180) * Math.PI) * r + x0,
          -Math.sin(((54 + i * 72) / 180) * Math.PI) * r + y0
        );
      }
      ctx.closePath();
      ctx.lineCap = "round";
      ctx.lineWidth = "10";
      ctx.lineJoin = "round";
      ctx.strokeStyle = "#522";

      ctx.stroke();
      ctx.restore();
    },
     drawpic(ctx, x0, y0, R) {
      ctx.save();
      var backImg=new Image()
      backImg.src=require("../assets/logo.png")
      backImg.onload=()=>{
        var patten=ctx.createPattern(backImg,"no-repeat")
        ctx.fillStyle=patten
        ctx.fillRect(x0,y0,R,R)
      }
      ctx.restore();
    },
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import "@/styles/global.scss";
/* a {
  color: #42b983;
} */
.drawimg0 {
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  .drawimg0Style {
    border: 1px red solid;
  }
}
</style>



